 }\r\n\r\n}\r\n\r\n\r\n// Collections\r\n.collection {\r\n  margin: $element-top-margin 0 $element-bottom-margin 0;\r\n  border: 1px solid $collection-border-color;\r\n  border-radius: 2px;\r\n  overflow: hidden;\r\n  position: relative;\r\n\r\n  .collection-item {\r\n    background-color: $collection-bg-color;\r\n    line-height: $collection-line-height;\r\n    padding: 10px 20px;\r\n    margin: 0;\r\n    border-bottom: 1px solid $collection-border-color;\r\n\r\n    // Avatar Collection\r\n    &.avatar {\r\n      min-height: 84px;\r\n      padding-left: 72px;\r\n      position: relative;\r\n\r\n      // Don't style circles inside preloader classes.\r\n      &:not(.circle-clipper) > .circle,\r\n      :not(.circle-clipper) > .circle {\r\n        position: absolute;\r\n        width: 42px;\r\n        height: 42px;\r\n        overflow: hidden;\r\n        left: 15px;\r\n        display: inline-block;\r\n        vertical-align: middle;\r\n      }\r\n      i.circle {\r\n        font-size: 18px;\r\n        line-height: 42px;\r\n        color: #fff;\r\n        background-color: #999;\r\n        text-align: center;\r\n      }\r\n\r\n\r\n      .title {\r\n        font-size: 16px;\r\n      }\r\n\r\n      p {\r\n        margin: 0;\r\n      }\r\n\r\n      .secondary-content {\r\n        position: absolute;\r\n        top: 16px;\r\n        right: 16px;\r\n      }\r\n\r\n    }\r\n\r\n\r\n    &:last-child {\r\n      border-bottom: none;\r\n    }\r\n\r\n    &.active {\r\n      background-color: $collection-active-bg-color;\r\n      color: $collection-active-color;\r\n\r\n      .secondary-content {\r\n        color: #fff;\r\n      }\r\n    }\r\n  }\r\n  a.collection-item{\r\n    display: block;\r\n    transition: .25s;\r\n    color: $collection-link-color;\r\n    &:not(.active) {\r\n      &:hover {\r\n        background-color: $collection-hover-bg-color;\r\n      }\r\n    }\r\n  }\r\n\r\n  &.with-header {\r\n    .collection-header {\r\n      background-color: $collection-bg-color;\r\n      border-bottom: 1px solid $collection-border-color;\r\n      padding: 10px 20px;\r\n    }\r\n    .collection-item {\r\n      padding-left: 30px;\r\n    }\r\n    .collection-item.avatar {\r\n      padding-left: 72px;\r\n    }\r\n  }\r\n\r\n}\r\n// Made less specific to allow easier overriding\r\n.secondary-content {\r\n  float: right;\r\n  color: $secondary-color;\r\n}\r\n.collapsible .collection {\r\n  margin: 0;\r\n  border: none;\r\n}\r\n\r\n\r\n\r\n// Responsive Videos\r\n.video-container {\r\n    position: relative;\r\n    padding-bottom: 56.25%;\r\n    height: 0;\r\n    overflow: hidden;\r\n\r\n    iframe, object, embed {\r\n      position: absolute;\r\n      top: 0;\r\n      left: 0;\r\n      width: 100%;\r\n      height: 100%;\r\n    }\r\n}\r\n\r\n// Progress Bar\r\n.progress {\r\n    position: relative;\r\n    height: 4px;\r\n    display: block;\r\n    width: 100%;\r\n    background-color: lighten($progress-bar-color, 40%);\r\n    border-radius: 2px;\r\n    margin: $element-top-margin 0 $element-bottom-margin 0;\r\n    overflow: hidden;\r\n  .determinate {\r\n    position: absolute;\r\n    top: 0;\r\n    left: 0;\r\n    bottom: 0;\r\n    background-color: $progress-bar-color;\r\n    transition: width .3s linear;\r\n  }\r\n  .indeterminate {\r\n    background-color: $progress-bar-color;\r\n    &:before {\r\n      content: '';\r\n      position: absolute;\r\n      background-color: inherit;\r\n      top: 0;\r\n      left:0;\r\n      bottom: 0;\r\n      will-change: left, right;\r\n      // Custom bezier\r\n      animation: indeterminate 2.1s cubic-bezier(0.650, 0.815, 0.735, 0.395) infinite;\r\n\r\n    }\r\n    &:after {\r\n      content: '';\r\n      position: absolute;\r\n      background-color: inherit;\r\n      top: 0;\r\n      left:0;\r\n      bottom: 0;\r\n      will-change: left, right;\r\n      // Custom bezier\r\n      animation: indeterminate-short 2.1s cubic-bezier(0.165, 0.840, 0.440, 1.000) infinite;\r\n      animation-delay: 1.15s;\r\n    }\r\n  }\r\n}\r\n@keyframes indeterminate {\r\n    0% {\r\n      left: -35%;\r\n      right:100%;\r\n    }\r\n    60% {\r\n      left: 100%;\r\n      right: -90%;\r\n    }\r\n    100% {\r\n      left: 100%;\r\n      right: -90%;\r\n    }\r\n}\r\n\r\n@keyframes indeterminate-short {\r\n    0% {\r\n      left: -200%;\r\n      right: 100%;\r\n    }\r\n    60% {\r\n      left: 107%;\r\n      right: -8%;\r\n    }\r\n    100% {\r\n      left: 107%;\r\n      right: -8%;\r\n    }\r\n}\r\n\r\n\r\n/*******************\r\n  Utility Classes\r\n*******************/\r\n\r\n.hide {\r\n  display: none !important;\r\n}\r\n\r\n// Text Align\r\n.left-align {\r\n  text-align: left;\r\n}\r\n.right-align {\r\n  text-align: right\r\n}\r\n.center, .center-align {\r\n  text-align: center;\r\n}\r\n\r\n.left {\r\n  float: left !important;\r\n}\r\n.right {\r\n  float: right !important;\r\n}\r\n\r\n// No Text Select\r\n.no-select {\r\n  user-select: none;\r\n}\r\n\r\n.circle {\r\n  border-radius: 50%;\r\n}\r\n\r\n.center-block {\r\n  display: block;\r\n  margin-left: auto;\r\n  margin-right: auto;\r\n}\r\n\r\n.truncate {\r\n  display: block;\r\n  white-space: nowrap;\r\n  overflow: hidden;\r\n  text-overflow: ellipsis;\r\n}\r\n\r\n.no-padding {\r\n  padding: 0 !important;\r\n}\r\n",".container {\r\n  margin: 0 auto;\r\n  max-width: 1280px;\r\n  width: 90%;\r\n}\r\n@media #{$medium-and-up} {\r\n  .container {\r\n    width: 85%;\r\n  }\r\n}\r\n@media #{$large-and-up} {\r\n  .container {\r\n    width: 70%;\r\n  }\r\n}\r\n.col .row {\r\n  margin-left: (-1 * $gutter-width / 2);\r\n  margin-right: (-1 * $gutter-width / 2);\r\n}\r\n\r\n.section {\r\n  padding-top: 1rem;\r\n  padding-bottom: 1rem;\r\n\r\n  &.no-pad {\r\n    padding: 0;\r\n  }\r\n  &.no-pad-bot {\r\n    padding-bottom: 0;\r\n  }\r\n  &.no-pad-top {\r\n    padding-top: 0;\r\n  }\r\n}\r\n\r\n\r\n// Mixins to eliminate code repitition\r\n@mixin reset-offset {\r\n  margin-left: auto;\r\n  left: auto;\r\n  right: auto;\r\n}\r\n@mixin grid-classes($size, $i, $perc) {\r\n  &.offset-#{$size}#{$i} {\r\n    margin-left: $perc;\r\n  }\r\n  &.pull-#{$size}#{$i} {\r\n    right: $perc;\r\n  }\r\n  &.push-#{$size}#{$i} {\r\n    left: $perc;\r\n  }\r\n}\r\n\r\n\r\n.row {\r\n  margin-left: auto;\r\n  margin-right: auto;\r\n  margin-bottom: 20px;\r\n\r\n  // Clear floating children\r\n  &:after {\r\n    content: \"\";\r\n    display: table;\r\n    clear: both;\r\n  }\r\n\r\n  .col {\r\n    float: left;\r\n    box-sizing: border-box;\r\n    padding: 0 $gutter-width / 2;\r\n    min-height: 1px;\r\n\r\n    &[class*=\"push-\"],\r\n    &[class*=\"pull-\"] {\r\n      position: relative;\r\n    }\r\n\r\n    $i: 1;\r\n    @while $i <= $num-cols {\r\n      $perc: unquote((100 / ($num-cols / $i)) + \"%\");\r\n      &.s#{$i} {\r\n        width: $perc;\r\n        @include reset-offset;\r\n      }\r\n      $i: $i + 1;\r\n    }\r\n\r\n    $i: 1;\r\n    @while $i <= $num-cols {\r\n      $perc: unquote((100 / ($num-cols / $i)) + \"%\");\r\n      @include grid-classes(\"s\", $i, $perc);\r\n      $i: $i + 1;\r\n    }\r\n\r\n    @media #{$medium-and-up} {\r\n\r\n      $i: 1;\r\n      @while $i <= $num-cols {\r\n        $perc: unquote((100 / ($num-cols / $i)) + \"%\");\r\n        &.m#{$i} {\r\n          width: $perc;\r\n          @include reset-offset;\r\n        }\r\n        $i: $i + 1\r\n      }\r\n\r\n      $i: 1;\r\n      @while $i <= $num-cols {\r\n        $perc: unquote((100 / ($num-cols / $i)) + \"%\");\r\n        @include grid-classes(\"m\", $i, $perc);\r\n        $i: $i + 1;\r\n      }\r\n    }\r\n\r\n    @media #{$large-and-up} {\r\n\r\n      $i: 1;\r\n      @while $i <= $num-cols {\r\n        $perc: unquote((100 / ($num-cols / $i)) + \"%\");\r\n        &.l#{$i} {\r\n          width: $perc;\r\n          @include reset-offset;\r\n        }\r\n        $i: $i + 1;\r\n      }\r\n\r\n      $i: 1;\r\n      @while $i <= $num-cols {\r\n        $perc: unquote((100 / ($num-cols / $i)) + \"%\");\r\n        @include grid-classes(\"l\", $i, $perc);\r\n        $i: $i + 1;\r\n      }\r\n    }\r\n\r\n    @media #{$extra-large-and-up} {\r\n\r\n      $i: 1;\r\n      @while $i <= $num-cols {\r\n        $perc: unquote((100 / ($num-cols / $i)) + \"%\");\r\n        &.xl#{$i} {\r\n          width: $perc;\r\n          @include reset-offset;\r\n        }\r\n        $i: $i + 1;\r\n      }\r\n\r\n      $i: 1;\r\n      @while $i <= $num-cols {\r\n        $perc: unquote((100 / ($num-cols / $i)) + \"%\");\r\n        @include grid-classes(\"xl\", $i, $perc);\r\n        $i: $i + 1;\r\n      }\r\n    }\r\n  }\r\n}\r\n","nav {\r\n  &.nav-extended {\r\n    height: auto;\r\n\r\n    .nav-wrapper {\r\n      min-height: $navbar-height-mobile;\r\n      height: auto;\r\n    }\r\n\r\n    .nav-content {\r\n      position: relative;\r\n      line-height: normal;\r\n    }\r\n  }\r\n\r\n  color: $navbar-font-color;\r\n  @extend .z-depth-1;\r\n  background-color: $primary-color;\r\n  width: 100%;\r\n  height: $navbar-height-mobile;\r\n  line-height: $navbar-line-height-mobile;\r\n\r\n  a { color: $navbar-font-color; }\r\n\r\n  i,\r\n  [class^=\"mdi-\"], [class*=\"mdi-\"],\r\n  i.material-icons {\r\n    display: block;\r\n    font-size: 24px;\r\n    height: $navbar-height-mobile;\r\n    line-height: $navbar-line-height-mobile;\r\n  }\r\n\r\n  .nav-wrapper {\r\n    position: relative;\r\n    height: 100%;\r\n  }\r\n\r\n  @media #{$large-and-up} {\r\n    a.sidenav-trigger { display: none; }\r\n  }\r\n\r\n\r\n  // Collapse button\r\n  .sidenav-trigger {\r\n    float: left;\r\n    position: relative;\r\n    z-index: 1;\r\n    height: $navbar-height-mobile;\r\n    margin: 0 18px;\r\n\r\n    i {\r\n      height: $navbar-height-mobile;\r\n      line-height: $navbar-line-height-mobile;\r\n    }\r\n  }\r\n\r\n\r\n  // Logo\r\n  .brand-logo {\r\n    position: absolute;\r\n    color: $navbar-font-color;\r\n    display: inline-block;\r\n    font-size: $navbar-brand-font-size;\r\n    padding: 0;\r\n\r\n    &.center {\r\n      left: 50%;\r\n      